import WAIDMatter from '../models/WAIDMatter'
import dayjs from 'dayjs'
import { getWAIDMatterList } from './WAIDMatterList'

export function getDateList() {
  let waidMatterList = getWAIDMatterList()
  let dateWAIDMatterObj = {}
  // 按天拆分每个事项记录的时间段记录
  // 只要开始时间在今天，那么整个时间段都放到今天
  waidMatterList.forEach(el => {
    let waidMatter = new WAIDMatter(el)
    waidMatter.recordList.forEach(el => {
      let date = dayjs(el.start).format('YYYY-MM-DD dddd')
      dateWAIDMatterObj[date] = dateWAIDMatterObj[date] || []
      let waidMatterTmp = new WAIDMatter(waidMatter)
      waidMatterTmp.recordList = [el]
      waidMatterTmp.startTime = el.start
      waidMatterTmp.updateRelativeStr(el)
      dateWAIDMatterObj[date].push(waidMatterTmp)
    })
  })

  let dateList = []
  for (let key in dateWAIDMatterObj) {
    dateWAIDMatterObj[key].sort((el0, el1) => {
      return el0.startTime - el1.startTime
    })
    dateList.push({
      date: key,
      waidMatters: dateWAIDMatterObj[key]
    })
  }
  dateList.sort((el0, el1) => {
    return dayjs(el0.date).isAfter(dayjs(el1.date)) ? -1 : 1
  })
  // console.log(dateList)
  return dateList
}
