/**
 * 首页 - 我在做些什么？！
 * @module /views/Index/index
 */

import './index.scss'
import React from 'react'
import { List, Icon, InputItem } from 'antd-mobile'
import WAIDMatter from '../../models/WAIDMatter'
import CAction from '../../components/CAction'
import { saveWAIDMatterList, getWAIDMatterByTitle } from '../../services/WAIDMatterList'
import { getData } from '../../services/Home'

const Item = List.Item

class Index extends React.Component {
  constructor(props) {
    super(props)
    const { list } = getData()
    this.state = {
      waidMatterList: list,
      pressingItemIndex: null
    }
    this.timerID = null
  }

  componentDidMount() {
    this.update()
  }

  componentDidUpdate(prevProps, prevState) {
    this.storeWAIDMatterList()
  }

  componentWillUnmount() {
    this.stopTimer()
  }

  update(index) {
    let { waidMatterList } = this.state

    if (index >= 0) {
      // 激活当前事项并置顶
      let waidMatter = waidMatterList[index]
      waidMatter.switchStatus()
      if (waidMatter.isActive()) {
        waidMatterList.splice(index, 1)
        waidMatterList.unshift(waidMatter)
      }
      this.setState({
        waidMatterList: waidMatterList
      })
    }

    // 列表项激活状态判定
    let actived = false
    waidMatterList.forEach(el => {
      actived = actived || el.isActive()
    })
    if (actived) {
      this.startTimer()
    } else {
      this.stopTimer()
    }
  }

  /**
   * 拆分激活和非激活状态事项，并更新激活状态事项显示时间
   */
  splitListByStatus() {
    let { waidMatterList } = this.state
    let _actives = []
    let _unactives = []
    waidMatterList.forEach(el => {
      if (el.isActive()) {
        el.updateRelativeStr()
        _actives.push(el)
      } else {
        _unactives.push(el)
      }
    })
    this.setState({
      waidMatterList: [..._actives, ..._unactives]
    })
  }

  startTimer() {
    if (this.timerID) return
    this.timerID = setInterval(() => {
      this.splitListByStatus()
    }, 1000)
  }

  stopTimer() {
    clearInterval(this.timerID)
    this.timerID = null
    this.storeWAIDMatterList()
  }

  storeWAIDMatterList() {
    saveWAIDMatterList(this.state.waidMatterList)
  }

  addItem() {
    let value = ''
    let isReject = true
    while (isReject) {
      value = window.prompt('输入事项名称', this.state.waidMatterList.length > 0 ? '' : '早餐')
      isReject = false
      if (!value && value !== null) {
        isReject = true
        alert('请输入正确的事项名称')
      }
      if (getWAIDMatterByTitle(this.state.waidMatterList, value)) {
        isReject = true
        alert('已有同名事项存在')
      }
    }
    if (value) {
      let { waidMatterList } = this.state
      let waidMatter = new WAIDMatter({
        title: value
      })
      waidMatter.visible = true
      waidMatterList.unshift(waidMatter)
      this.setState({
        waidMatterList: waidMatterList
      })
    }
  }

  setPressingItem(item) {
    this.setState({
      pressingItemIndex: item
    })
  }

  filterListByKeyWord(keyWord) {
    let waidMatterList = this.state.waidMatterList
    waidMatterList.forEach(item => {
      item.visible = item.title.indexOf(keyWord) >= 0 || !keyWord
    })
    this.setState({
      waidMatterList: waidMatterList
    })
  }

  gotoDetail(item) {
    this.props.history.replace('/detail/' + encodeURIComponent(item.title))
  }

  render() {
    return (
      <section className="index">
        <div className="index-header">
          <div className="index-header-title">我在做些什么？!</div>
          <InputItem
            clear
            placeholder="输入关键字进行搜索"
            onChange={value => {
              this.filterListByKeyWord(value)
            }}
          />
        </div>
        {/* 事项列表 */}
        <List className="index-list">
          {this.state.waidMatterList.map((item, index) =>
            item.visible ? (
              <CAction
                onLongPress={() => {
                  this.update(index)
                  this.setPressingItem()
                }}
                onTouchStart={() => {
                  this.setPressingItem(index)
                }}
                onTouchMove={() => {
                  this.setPressingItem()
                }}
                onTouchEnd={() => {
                  this.setPressingItem()
                }}
                onClick={() => {
                  this.gotoDetail(item)
                }}
                key={index}
              >
                <Item extra={item.relativeStr} className={[item.isActive() && 'index-list-item-active', this.state.pressingItemIndex === index && 'index-list-item-pressing']}>
                  <span className="index-list-item-title">{item.title}</span>
                </Item>
              </CAction>
            ) : (
              ''
            )
          )}
        </List>
        {/* 添加按钮 */}
        <div className="index-add float-btn" onClick={() => this.addItem()}>
          <Icon type="cross" size="lg" color="#666" />
        </div>
      </section>
    )
  }
}

export default Index
