/**
 * 详情
 * @module /views/Detail/index
 */

import './index.scss'
import React from 'react'
import { List } from 'antd-mobile'
import { getMatterByTitle } from '../../services/Detail'

const Item = List.Item

class Detail extends React.Component {
  constructor(props) {
    super(props)
    const title = this.props.match.params.title
    this.state = {
      title,
      matter: getMatterByTitle(title)
    }
  }

  render() {
    return (
      <section className="detail">
        <div className="detail-header">
          <p className="detail-title">{this.state.matter.title}</p>
          <p className="detail-sub-title">{this.state.matter.createAtStr}</p>
        </div>
        <List className="detail-record-list">
          {this.state.matter.recordList.map((record, index) => (
            <Item key={index}>
              <p className="detail-record-title">
                {index + 1}. {record.relativeStr}
              </p>
              <p className="detail-record-info">{record.startStr}</p>
              <p className="detail-record-info">{record.endStr}</p>
            </Item>
          ))}
        </List>
        <div className="detail-footer">-- 到底了 --</div>
      </section>
    )
  }
}

export default Detail
