/**
 * WAID 事件
 * @module /models/WAIDMatter
 */

import WAIDMatterRecord from './WAIDMatterRecord'

/**
 * WAID 事件
 */
export default class WAIDMatter {
  constructor(waidMatter = {}) {
    this.title = waidMatter.title || ''
    this.recordList = waidMatter.recordList || waidMatter.timeList || []
    this.createAt = waidMatter.createAt || (this.recordList[0] || {}).start || Date.now()
    this.relativeStr = ''
    this.checkRecordList()
    this.updateRelativeStr()
  }

  checkRecordList() {
    let recordList = []
    this.recordList.forEach(el => {
      if (el.start !== 'undefined' && typeof el.end !== 'undefined') {
        recordList.push(new WAIDMatterRecord(el))
      }
    })
    this.recordList = recordList
  }

  switchStatus() {
    let _WAIDMatterRecord = this.getLastRecord()
    if (!_WAIDMatterRecord.start || (_WAIDMatterRecord.start && _WAIDMatterRecord.end)) {
      _WAIDMatterRecord = new WAIDMatterRecord({
        start: Date.now(),
        end: 0
      })
      this.recordList.push(_WAIDMatterRecord)
    } else if (!_WAIDMatterRecord.stop) {
      this.getLastRecord().end = Date.now()
    }
  }

  updateRelativeStr(record) {
    try {
      record = record || this.getLastRecord()
      this.relativeStr = record.getDateTimeString()
    } catch (err) {}
  }

  getLastRecord() {
    return this.recordList[this.recordList.length - 1] || {}
  }

  isActive() {
    return this.getLastRecord().end === 0
  }

  getStoreObject() {
    return {
      title: this.title,
      recordList: this.recordList,
      createAt: this.createAt
    }
  }
}
