import dayjs from 'dayjs'
import { getWAIDMatterByTitle } from './WAIDMatterList'

export function getMatterByTitle(title = '') {
  let waidMatter = null
  try {
    waidMatter = getWAIDMatterByTitle(null, title)
    waidMatter.recordList.forEach(el => {
      el.relativeStr = el.getDateTimeString()
      el.startStr = dayjs(el.start).format('YYYY-MM-DD hh:mm:ss')
      el.endStr = dayjs(el.end).format('YYYY-MM-DD hh:mm:ss')
    })
    waidMatter.recordList.reverse()
    waidMatter.createAtStr = dayjs(waidMatter.createAt).format('YYYY-MM-DD hh:mm:ss')
  } catch (err) {}
  return waidMatter
}
