/**
 * 设置
 * @module /views/Setting/index
 */

import './index.scss'
import React from 'react'
import { List } from 'antd-mobile'
import { set as setTitle } from '../../uitls/title'
import { version } from '../../uitls/version'

const Item = List.Item

class Setting extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      list: [
        // {
        //   label: '统计',
        //   path: '/analysis'
        // },
        {
          label: '备份与恢复',
          path: '/migrateandrecover'
        }
      ]
    }
  }

  render() {
    return (
      <section className="setting flex-column">
        <List className="setting-list">
          {this.state.list.map((item, dateIndex) => (
            <Item
              arrow="horizontal"
              onClick={() => {
                setTitle(item.label)
                window.location.replace('#' + item.path)
              }}
              key={dateIndex}
            >
              {item.label}
            </Item>
          ))}
        </List>
        <div className="footer">version {version}</div>
      </section>
    )
  }
}

export default Setting
