/**
 * WAIDMatterList 数据获取、存储模块
 * @module /services/WAIDMatterList
 */

import { Local } from '../uitls/Store'
import { LocalKeys } from '../config'
import WAIDMatter from '../models/WAIDMatter'

const LocalKey = LocalKeys.WAIDMatterList

/**
 * 获取事项列表
 * @returns {Array}
 */
export function getWAIDMatterList() {
  let localWAIDMatterList = Local.get(LocalKey) || []
  const WAIDMatterList = []
  localWAIDMatterList.forEach(el => {
    // 不处理标题为空事项
    if (el.title === '') {
      return
    }
    // 合并同名事项
    const existMatter = getWAIDMatterByTitle(WAIDMatterList, el.title)
    const tmpMatter = new WAIDMatter(el)
    if (existMatter) {
      existMatter.recordList = [...existMatter.recordList, ...tmpMatter.recordList]
      existMatter.recordList.sort((el0, el1) => {
        return el0.start - el1.start
      })
      return
    }
    // 插入
    WAIDMatterList.push(tmpMatter)
  })
  return WAIDMatterList
}

/**
 * 根据标题获取事项
 * @param {Array} WAIDMatterList 事项列表
 * @param {String} title 标题
 * @returns {Object}
 */
export function getWAIDMatterByTitle(WAIDMatterList = [], title = '') {
  WAIDMatterList = WAIDMatterList instanceof Array ? WAIDMatterList : getWAIDMatterList()
  return WAIDMatterList.find(el => {
    return el.title === title
  })
}

/**
 * 保存事项列表
 * @param {Array} WAIDMatterList 事项列表
 */
export function saveWAIDMatterList(waidMatterList = []) {
  const storeWaidMatterList = []
  waidMatterList.forEach(el => {
    storeWaidMatterList.push(el.getStoreObject())
  })
  Local.set(LocalKey, storeWaidMatterList)
}
