/**
 * 日常
 * @module /views/Daily/index
 */

import './index.scss'
import React from 'react'
import { List } from 'antd-mobile'
import { getDateList } from '../../services/Daily'

const Item = List.Item

class Daily extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      waidMatterList: getDateList()
    }
  }

  render() {
    return (
      <section className="daily">
        {this.state.waidMatterList.map((dateItem, dateIndex) => (
          <div className="dailiy-list-box" key={dateIndex}>
            <div className="daily-list-header" style={{ zIndex: dateIndex + 99 }}>
              {dateItem.date}
            </div>
            <List className="daily-list">
              {dateItem.waidMatters.map((waidMatter, index) => (
                <Item extra={waidMatter.relativeStr} key={index}>
                  {waidMatter.title}
                </Item>
              ))}
            </List>
          </div>
        ))}
      </section>
    )
  }
}

export default Daily
